<template>
  <div class="center-supply-account">
    <div class="list-wrap">
      <div class="search-bar">
        <!-- <div class="search-bar-title"></div>
        <div class="form-wrap">
          <div class="form-name"></div>
          <select  class="form-control"></select>
        </div>
        <button class="btn-fill-main"></button> -->
        <button class="btn-main" @click="addSupply()"></button>
      </div>
      <table class="table table-text">
         <thead>
           <tr>
             <!-- <th class="table-w100">ID</th> -->
             <th class="table-w220"></th>
             <th class="table-w100"></th>
             <th class="table-w160"></th>
             <!-- <th class="table-w180"></th> -->
             <th class="table-w180"></th>
             <th class="table-w120"></th>         
             <!-- <th class="table-w180" v-show="!userConfig.singleChannel"></th>          -->
             <th ></th>
           </tr>
         </thead>
         <tbody>
          <tr v-for="item in listData" :key="item.id">
            <td>
              <el-popover
                placement="top"
                width="260"
                trigger="hover">
                <span>{{item.supplierName}}</span>
                <span class="ellipsis" slot="reference">{{item.supplierName}}</span>
              </el-popover>
            </td>
            <td>{{item.itemNum}}</td>
            <td>{{item.apiTypeName}}</td>
            <td>
              <el-popover 
                placement="top"
                width="260"
                trigger="hover">
                <span>{{item.channelTypeNames}}</span>
                <span class="ellipsis" slot="reference">{{item.channelTypeNames}}</span>
              </el-popover>
            </td>
            <td>{{item.status ? '' : ''}}</td>
            <td>
              <a class="link" @click="editItem(item)"></a>
              <a class="link" @click="delItem(item)"></a>
            </td>
          </tr>
          </tbody>
      </table>
      <pagination v-show="total>0" :total="total" :page.sync="listParams.page" :limit.sync="listParams.limit" @pagination="getList(listParams.page)" />
      <noData v-if="total===0"></noData>
    </div>
    <el-dialog :title="isEdit?'':''" :visible.sync="modalShow" width="590px">
      <div class="dialog-form">
        <el-form ref="form" :rules="rules" :model="form" label-width="110px">
          <div class="form-gorup-content">
            <div class="form-group-name"></div>
            <el-form-item label=""  prop="supplierName">
              <el-input v-model="form.supplierName" size="mini"></el-input>
            </el-form-item>
            <el-form-item label="" v-if="apiTypeList.length>1">
              <el-select v-model="form.apiType" size="mini" placeholder="" value-key="type">
                <el-option :label="item.name" :value="item.type" v-for="item in apiTypeList" :key="item.type+'type'"></el-option>
              </el-select>
            </el-form-item>
<!--            <p>{{form.sourceItemAttr.channelTypes}}</p>-->
            <el-form-item label="" prop="sourceItemAttr.channelTypes" v-if="form.apiType==0">
              <el-select v-model="form.sourceItemAttr.channelTypes" class="select-multiple" multiple size="mini" placeholder="">
                <el-option v-for="item in channelList"
                    :key="item.channelType"
                    :label="item.channelTypeName"
                    :value="item.channelType">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="status">
            <el-radio-group v-model="form.status">
              <el-radio :label="1"></el-radio>
              <el-radio :label="0"></el-radio>
            </el-radio-group>
          </el-form-item>
          </div>
          <div v-if="form.apiType">
            <div class="form-gorup-content">
              <div class="form-group-name"></div>
              <el-form-item label="App Id">
                <el-input v-model="form.apiAuth.appId" size="mini" class="appId"></el-input>
              </el-form-item>
              <el-form-item label="App Secret">
                <el-input v-model="form.apiAuth.secretKey" size="mini"></el-input>
              </el-form-item>
            </div>
            <div class="form-gorup-content">

              <div class="form-group-name"></div>
              <el-form-item label="">

                <el-select v-model="form.sourceItemAttr.channelTypes" class="select-multiple" multiple size="mini" placeholder="">
                  <el-option v-for="item in channelList"
                      :key="item.channelType"
                      :label="item.channelTypeName"
                      :value="item.channelType"></el-option>
                </el-select>
              </el-form-item>
            </div>
            <div class="form-gorup-content no-border">
              <div class="form-group-name"></div>
              <el-form-item label="">
                <el-select v-model="form.targetItemAttr.shopId" size="mini" placeholder="">
                  <el-option :label="item.name" :value="item.id" v-for="item in shopList" :key="item.id"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-select v-model="form.targetItemAttr.expressFeeTemplateId" size="mini" placeholder="">
                  <el-option :label="item.templateName" :value="item.id" v-for="item in feetemplate" :key="item.id"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-select v-model="form.priceStrategy.type" size="mini" placeholder="">
                  <el-option label="" :value="1"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="form.priceStrategy.priceUpPercent" size="mini"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="form.priceStrategy.originalPriceUpPercent" size="mini"></el-input>
              </el-form-item>
            </div>
          </div>
          <el-form-item>
            <div class="btn-org-main" type="primary" @click="saveSupplier('form')"></div>
            <div class="btn-fill-grey-main" @click="modalShow = false"></div>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>


  </div>
</template>
<script>
import Pagination from '@/components/pagination';
import noData from '@/components/noData';
export default {
  data() {
    return {
      modalShow:false,
      total: -1,
      listData: [],
      isEdit: false,
      channelList: [],
      feetemplate:[],
      shopList:[],
      listParams: {
        page: 1,
        limit: 10
      },
      form:{
        supplierName:'',
        apiType:0,
        apiAuth:{
          appId:'',
          secretKey:''
        },
        targetItemAttr:{
          shopId:'',
          expressFeeTemplateId:''
        },
        sourceItemAttr:{
          channelTypes:[]
        },
        priceStrategy:{
          type:1,
          priceUpPercent:'',
          originalPriceUpPercent:''
        },
        status:1
      },
      apiTypeList:[{name:'',type:0}],
      rules:{
        supplierName: [{ required: true, message: '', trigger: 'blur' }],
        'sourceItemAttr.channelTypes': [{ type: 'array',required: true, message: '', trigger: 'change' }]
      },
      dataTemp:[],// 

    }
  },
  created() {
    this.init();
  },
  methods:{
    init() {
      this.getList(1);
      this.api.goods.channel().then((res) => {
        this.channelList = res.data;
        this.channelList.forEach(item => {
          this.$set(item, 'checked', true);
          this.dataTemp.push(item.channelType) //
        });
      });
      this.api.supplier.apiType().then(res=>{
        this.apiTypeList = this.apiTypeList.concat(res.data);
      });
      this.api.feetemplate.list({limit:9999}).then((res) => {
        this.feetemplate = res.data.dataList;
      });
      this.api.store.list(this.listParams).then((res) => {
        this.shopList = res.data.dataList;
      });
    },
    //
    getList(page) {
      this.listParams.page = page;
      this.api.supplier.list(this.listParams).then((res) => {
        this.listData = res.data.dataList;
        this.total = res.data.total;
      });
    },
    //
    addSupply() {
      this.isEdit = false;
      this.modalShow = true;
      this.form = {
        supplierName:'',
        apiType:0,
        apiAuth:{
          appId:'',
          secretKey:''
        },
        targetItemAttr:{
          shopId:'',
          expressFeeTemplateId:''
        },
        sourceItemAttr:{
          channelTypes:[]
        },
        priceStrategy:{
          type:1,
          priceUpPercent:'',
          originalPriceUpPercent:''
        },
        status:1
      };
      this.form.sourceItemAttr.channelTypes=JSON.parse(JSON.stringify(this.dataTemp)); //dataTemp

    },

    //
    editItem(item) {
      this.isEdit = true;
      this.api.supplier.detail({id: item.id}).then((res) => {
        console.log(res.data);
        // this.form = res.data;
        this.form = Object.assign({},this.form,res.data);
        this.form.sourceItemAttr = {
            channelTypes:[]
          };
        if(res.data.channelTypes){
          res.data.channelTypes.forEach(data => {
            this.form.sourceItemAttr.channelTypes.push(data.channelType);
          });
          
        } else {
          this.form.sourceItemAttr.channelTypes = res.data.sourceItemAttr.channelTypes;
        }
        this.modalShow = true;
        
      });
    },
    //  
    saveSupplier(formName) {
      let params = this.copy(this.form);
      console.log(params);
      // let params = this.form;
      
      //
      let CHarr = [];
      if(params.sourceItemAttr.channelTypes.length){
        params.sourceItemAttr.channelTypes.forEach((item)=>{
          CHarr.push(item);
        })
      }
      params.channelTypes = CHarr;
      // let arr = []
      // params.sourceItemAttr.channelTypes.forEach(val=>{
      //  val.channelType ? arr.push(val.channelType): ''
      // })
      // params.sourceItemAttr.channelTypes = arr
      let api = params.id ? 'edit' : 'add';
      if(!params.id) {
        delete params.id;
      }
      if(!params.apiType){
        delete params.apiAuth;
        delete params.targetItemAttr;
        delete params.priceStrategy;
        delete params.sourceItemAttr;
        // params.apiAuth = {
        //   appId:'',
        //   secretKey:''
        // };
        // params.targetItemAttr = {
        //   shopId:'',
        //   expressFeeTemplateId:''
        // };
        // params.priceStrategy = {
        //   type:1,
        //   priceUpPercent:'',
        //   originalPriceUpPercent:''
        // };
        // params.sourceItemAttr = {
        //   channelTypes:[]
        // };
      } else {
        delete params.channelTypes;
        // params.channelTypes = [];
      }
      
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.api.supplier[api](params).then((res) => {
            this.$message.success({message:res.message, showClose:true});
            this.modalShow = false;
            this.getList(1);
          });
        } else {
          console.log('error submit!!');
        }
      });
    },
    //
    delItem(item) {
      this.$msgbox({
        title: '',
        message: '',
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(action => {
        if(action === 'confirm') {
          this.api.supplier.del({id: item.id}).then((res) => {
            this.$message.success({message:res.message, showClose:true});
            this.getList(1);
          });
        }
      });
    }
  },
  components: {
    Pagination,
    noData
  }
}
</script>
<style lang='scss'>
  .center-supply-account{
    .table{
      .link{
        margin-right: 10px;
      }
    }
    .dialog-form{
      .form-name{
        width: 60px;
        text-align: right;
        vertical-align: top;
        line-height: 30px;
      }
      .form-control{
        width: 320px;
      }
      .channel-control{
        display: inline-block;
        vertical-align: top;
        width: 320px;
        .el-checkbox{
          line-height: 30px;
          margin-right: 15px;
        }
        .el-checkbox+.el-checkbox{
          margin-left: 0;
        }
      }
    }
    .form-gorup-content{
      margin-bottom: 15px;
      // border-bottom:1px solid rgba(229,229,229,1);
    }
    .form-group-name{
      margin-bottom: 15px;
      padding-left: 8px;
      font-size: 14px;
      font-weight: 600;
      position: relative;
      &::before{
        content: " ";
        position: absolute;
        height: 13px;
        width: 4px;
        left: 0;
        background: #F06941;
      }
    }
    .el-form-item{
      margin-bottom: 13px;
      .el-form-item__error {
        padding-top: 0;
      }
      .el-form-item__content{
        // margin-left:167px!important;
        .btn-org-main{
          background:#E1376C;
          margin-left:63px;
        }
        .el-input--mini{
          width: 400px;
        }
        .btn-org-main{
          margin-right:20px;
        }
        .select-multiple{
          margin-bottom: 10px;
        }
      }
      .appId,.itemInfo{
        display: inline-block;
      }
      .itemInfo{
        margin-left: 10px;
      }
    }
    .no-border{
      border:0;
    }
  }
  .remark-textarea{
    height: 500px;
  }


</style>
