<template>
  <div class="system-keyword">
    <div class="search-bar">
      <div class="form-wrap">
        <div class="form-name"></div>
        <select class="form-control" v-model="listParams.status" :class="{'unset-select':listParams.status===''}">
          <option value=""></option>
          <option v-for="item in statusList" :key="item.id" :value="item.id">{{item.name}}</option>
        </select>
      </div>
      <div class="form-wrap">
        <div class="form-name"></div>
        <input type="text" class="form-control" placeholder="" v-model="listParams.name">
      </div>
      <a class="btn-fill-main" @click="getList()"></a>
      <a class="btn-main" @click="addKeyword()"></a>
      <div class="pull-right">
        <a class="btn-fill-main" @click="batchDel()"></a>
      </div>
    </div>
    <table class="table table-text">
      <thead>
        <tr>
          <th class="dot" :class="{'active':checkAll}" @click="selectAll()"></th>
          <th class="table-w120"></th>
          <th class="table-w120"></th>
          <th class="table-w180"></th>
          <th class="table-w80"></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in listData" :key="index+'list'" :class="{'selectedTr':item.checked}">
          <td class="dot" :class="{'active':item.checked}" @click="selectItem(item)"></td>
          <td>{{item.name}}</td>
          <td>{{item.typeName}}</td>
          <td>
            <el-popover class="ellipsis"
              placement="right"
              width="260"
              trigger="hover">
              <span>{{item.jumpValue}}</span>
              <span slot="reference">{{item.jumpValue}}</span>
            </el-popover>
          </td>
          <td>
            <span v-show="item.status"></span>
            <span v-show="!item.status"></span>
          </td>
          <td>
            <a class="link" v-show="!item.status" @click="setStatue(item, 1)"></a>
            <a class="link" v-show="item.status" @click="setStatue(item, 0)"></a>
            <a class="link" @click="editKeyword(item)"></a>
            <a class="link" @click="delItem(item)"></a>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination v-show="total>0" :total="total" :page.sync="listParams.page" :limit.sync="listParams.limit" @pagination="getList()" />
    <noData v-if="total===0"></noData>
    <el-dialog :title="isEdit?'':''" :visible.sync="modalShow" width="450px">
      <div class="dialog-form">
        <div class="form-wrap">
          <div class="form-name"></div>
          <input class="form-control" placeholder="" v-model="modalParams.name"/>
        </div>
        <div class="form-wrap">
          <div class="form-name"></div>
          <select class="form-control" v-model="modalParams.jumpType" :class="{'unset-select':!modalParams.jumpType}">
            <option value=""></option>
            <option v-for="item in typeList" :key="item.code" :value="item.code">{{item.remark}}</option>
          </select>
        </div>
        <div class="form-wrap" v-if="modalParams.jumpType===24 || modalParams.jumpType===26">
          <div class="form-name"></div>
          <input class="form-control" :placeholder="modalParams.jumpType===24?'':'ID'" v-model="modalParams.jumpValue"/>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <button class="btn-org-main" @click="saveKeyword()"></button>
        <button class="btn-fill-grey-main" @click="modalShow = false"></button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Pagination from '@/components/pagination';
import noData from '@/components/noData';
export default {
  data() {
    return {
      modalShow: false,
      isEdit: false,
      checkAll: false,
      typeList: [],
      statusList: [
        {id:1,name:''},
        {id:0,name:''}
      ],
      listParams: {
        page:1,
        limit:10,
        status:'',
        name:''
      },
      modalParams: {
        jumpType: ''
      },
      listData: [],
      total: -1
    }
  },
  created() {
    this.init();
  },
  methods: {
    init() {
      // 
      this.api.hotKeyword.typeList({}).then((res) => {
        this.typeList = res.data;
      });
      this.getList();
    },
    getList() {
      this.api.hotKeyword.list(this.listParams).then((res) => {
        this.listData = res.data.dataList;
        this.total = res.data.total;
        this.listData.forEach(item => {
          this.typeList.forEach(type => {
            if(item.jumpType === type.code) {
              item.typeName = type.remark;
            }
          });
        });
      });
    },
    addKeyword() {
      this.modalParams = {
        jumpType:''
      };
      this.isEdit = false;
      this.modalShow = true;
    },
    editKeyword(item) {
      this.api.hotKeyword.detail({id: item.id}).then((res) => {
        this.modalParams = {
          id: res.data.id,
          name: res.data.name,
          jumpType: res.data.jumpType || '',
          jumpValue: res.data.jumpValue
        };
        this.isEdit = true;
        this.modalShow = true;
      });
    },
    delItem(item) {
      this.$msgbox({
        title: '',
        message: '',
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(action => {
        if(action === 'confirm') {
          this.api.hotKeyword.del({id: item.id}).then((res) => {
            this.$message.success({message: res.message, showClose: true});
            this.getList();
          });
        }
      });
    },
    setStatue(item, status) {
      this.$msgbox({
        title: '',
        message: status === 1 ? '' : '',
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(action => {
        if(action === 'confirm') {
          this.api.hotKeyword.setStatus({id: item.id, status: status}).then((res) => {
            this.$message.success({message: res.message, showClose: true});
            this.getList();
          });
        }
      });
    },
    saveKeyword() {
      let api = this.modalParams.id ? 'edit' : 'add';
      if(this.modalParams.jumpType === 8) {
        this.modalParams.jumpValue = '/';
      }
      this.api.hotKeyword[api](this.modalParams).then((res) => {
        this.$message.success({message: res.message, showClose: true});
        this.modalShow = false;
        this.getList();
      });
    },
    selectItem(item) {
      this.checkAll = true;
      item.checked = !item.checked;
      this.listData.forEach((item) => {
        if(!item.checked) {
          this.checkAll = false;
        }
      });
    },
    selectAll() {
      this.checkAll = !this.checkAll;
      this.listData.forEach((item) => {
        if(this.checkAll) {
          item.checked = true;
        } else {
          item.checked = false;
        }
      });
    },
    batchDel() {
      let selected = [];
      this.listData.forEach(item => {
        if(item.checked) {
          selected.push(item.id);
        }
      });
      if(selected.length === 0) {
        this.$message.info({message:'', showClose: true});
      } else {
        let ids = selected.join(',');
        this.$msgbox({
          title: '',
          message: '',
          showCancelButton: true,
          confirmButtonText: '',
          cancelButtonText: ''
        }).then(action => {
          if(action === 'confirm') {
            this.api.hotKeyword.batchDel({ids: ids}).then((res) => {
              this.$message.success({message: res.message, showClose: true});
              this.getList();
            });
          }
        });
      }
    }
  },
  components: {
    Pagination,
    'noData': noData
  }
}
</script>

<style lang="scss" scoped>
  .system-keyword{
    background: #fff;
    .search-bar{
      .btn-main{
        margin-left: 20px;
      }
    }
    .table{
      .link{
        margin-right: 10px;
      }
    }
    .dialog-form{
      .form-wrap{
        .form-name{
          display: inline-block;
          vertical-align: top;
          width: 70px;
          line-height: 30px;
          text-align: left;
        }
        .form-control{
          width: 260px;
        }
      }
    }
  }
</style>
